import { mapMutations, mapState } from 'vuex';
export default {
	data() {
		return {
			avatar: '',
			head_img:'',
			info: null
		};
	},
	computed: {
		...mapState(['userInfo'])
	},
	onLoad() {
		this.$Fetch({
			url: 'user/getDataById',
			success: res => {
				console.log(res);
				this.info = res;
				this.avatar=res.head_img
			}
		});
	},
	methods: {
		...mapMutations(['setUserInfo']),
		//提交用户信息
		submitInfo: function(e) {
			console.log('form发生了submit事件，携带数据为：', e.detail.value);
			this.$Fetch({
				url: 'user/edit',
				data: e.detail.value,
				debug: true,
				success: res => {
					uni.showToast({
						title: '提交成功',
						icon: 'success'
					});
					setTimeout(() => {
						uni.navigateBack({
							delta: 1
						});
					}, 1500);
				}
			});
		},
		//选择头像
		chooseImage() {
			var that = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success(res) {
					// tempFilePath可以作为img标签的src属性显示图片
					const tempFilePath = res.tempFilePaths[0];

					that.avatar = tempFilePath;
					uni.showLoading({
						title:"上传中..."
					})
					that.$UploadFile({
						url: 'Upload/images',
						filePath: tempFilePath,
						name: 'file',
						debug: true,
						success: ret => {
							console.log(ret);
							uni.showToast({
								title:"上传成功",
								icon:"success"
							})
							that.head_img=ret
						}
					});
				}
			});
		}
	}
};
